.page {
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  color: $main-text-color;
  line-height: 140%;
  scroll-behavior: smooth;
  background-color: $color-bg-aside;

  :has(.page__menu:target) {
    overflow: hidden;
  }

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    opacity: 0;
    transition: all 0.3s;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}

h3,
p,
header {
  margin: 0;
}
